  }
  
  .payment-methods {
    grid-template-columns: repeat(3, 1fr);
  }
  
  .form-actions {
    flex-direction: column;
  }
  
  .form-actions .btn {
    width: 100%;
  }
}

@media (max-width: 480px) {
  .modal-header h2 {
    font-size: 1.2rem;
  }
  
  .payment-methods {
    grid-template-columns: repeat(2, 1fr);
  }
  
  .summary-details {
    font-size: 14px;
  }
}